.Eventos .mbn {
  margin-bottom: -14px; }

.Eventos__ano {
  display: inline-block;
  font-family: 'Lowan';
  margin: 0 auto;
  color: #9D8855;
  border: 1px solid #9D8855;
  padding: 14px 50px;
  font-size: 30px; }

@media screen and (max-width: 992px) {
  .Eventos__ano {
    font-size: 23px;
    padding: 11px 40px;
    margin-top: 16px;
    margin-bottom: 0px; } }

.Eventos__table {
  width: 100%; }

.Eventos__table-header {
  display: flex;
  padding-bottom: 14px;
  border-bottom: 1px solid #9D8855; }

.Eventos__table-header.mobile {
  display: none; }

@media screen and (max-width: 992px) {
  .Eventos__table-header.mobile {
    display: flex;
    width: 100%;
    margin-bottom: 10px;
    padding: 0 8px;
    padding-bottom: 5px;
    margin-top: 10px; } }

@media screen and (max-width: 992px) {
  .Eventos__table-header {
    padding-bottom: 8px;
    display: none; } }

.Eventos__table-header div {
  color: #9D8855;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: lighter;
  width: 26.7%; }

@media screen and (max-width: 992px) {
  .Eventos__table-header div {
    font-size: 17px; } }

.Eventos__table-header div:nth-last-child(2) {
  width: 26.2%; }

@media screen and (max-width: 992px) {
  .Eventos__table-header div:nth-last-child(2) {
    min-width: 37%; } }

.Eventos__table-datas {
  width: 100%; }

.Eventos__table-item {
  display: flex;
  width: 100%;
  border-bottom: 1px solid #D6D6D6;
  padding: 10px 0; }

.Eventos__table-item:last-child {
  margin-bottom: 15px; }

.Eventos__table-item .data {
  font-weight: 500;
  color: #9D8855;
  font-size: 21px;
  min-width: 15%;
  display: flex;
  align-items: center; }

@media screen and (max-width: 992px) {
  .Eventos__table-item .data {
    font-size: 15px;
    text-align: center;
    text-align: left;
    margin-left: -5px;
    min-width: 28%;
    padding-left: 14px;
    padding-right: 20px; } }

.Eventos__table-item .evento {
  min-width: 30.5%;
  color: #484848;
  font-size: 15px;
  font-weight: 500;
  padding-right: 40px;
  display: flex;
  align-items: center; }

@media screen and (max-width: 992px) {
  .Eventos__table-item .evento {
    min-width: 35%;
    padding-right: 0; } }

.Eventos__table-item .local {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  font-size: 15px;
  color: #484848bb; }

@media screen and (max-width: 992px) {
  .Eventos__table-item .local {
    padding-right: 15px; } }

.Eventos__item {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 26px 0;
  padding-bottom: 10px;
  border-bottom: 1px solid #9d8755bb; }

@media screen and (max-width: 992px) {
  .Eventos__item {
    flex-direction: column; } }

.Eventos__item-mes {
  min-width: 14%;
  font-weight: 500;
  font-size: 23px;
  color: #9D8855; }

@media screen and (max-width: 992px) {
  .Eventos__item-mes {
    width: 100%;
    text-align: center;
    margin-bottom: 15px;
    text-transform: uppercase; } }
